@font-face {
    font-family: 'Dolce Vita Heavy A Few Extra Pounds';
    font-style: normal;
    font-weight: normal;
    src: local('Dolce Vita Heavy A Few Extra Pounds'), url('../dolce vita heavy bold.woff') format('woff');
    }
 .Home .lander {
    position: relative;
    padding: 80px 0;
    top:-80px;
    text-align: center;

  }
  svg{
    width: 80%;
    fill: rgba(24, 24, 24, 0.459);
    opacity: 0;
    stroke: rgb(0, 0, 0);
    stroke-dasharray: 500;
  stroke-dashoffset: 500;
  animation: animate 5s forwards;

  }
  @keyframes animate{
    to{
      stroke-dashoffset: 1;
      
      opacity: 1;
    }
  }

  
